.goods-count{font-size:16px;display:inline-flex;justify-content:space-between;align-items:center;background-color:#fff}.goods-count .minus,.goods-count .plus{width:20px;height:20px;background-size:20px 20px;position:relative}.goods-count .minus{margin-right:12.5px;background-image:url(../../assists/imgs/减号.svg);background-repeat:no-repeat;background-position:center center}.goods-count .plus{margin-left:12.5px;background-image:url(../../assists/imgs/加号.svg);background-repeat:no-repeat;background-position:center center}